.nav{
    background-color: #d3d3d3;
    position: fixed;
    width: 100%;
    margin-bottom: 100px;
}   

.nav-item{
    width: 3rem;
    height: 3rem;
    -webkit-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s
}

.nav-item:hover{
    background-color: #1c2777 
    
}


